    <el-select class="valueid" v-model="value"  placeholder="ID">
      <el-option
          v-for="item in cities"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        <span style="float: left">{{ item.label }}</span>
        <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
      </el-option>
    </el-select>
    <el-button class="xssbut" @click="handelxss" type="danger">XSS</el-button>
    <el-button class="xssbuts" @click="handelelxss" type="danger"></el-button>
    <el-button class="xssbuts" @click="handelelelxss" type="primary"></el-button>


    <div class="xss0">
      <table class="tables" border='1' width="100%" >
        <tr align="center">
          <td>
            XSS
          </td>
          <td>
            
          </td>
          <td>
            ID
          </td>
          <td>
            
          </td>

        </tr>
        <tr align="center">
          <td>
            XSS
          </td>
          <td>
            {{XSS0[0].ClassificationName}}
          </td>
          <td>
            {{XSS0[0].ClassificationID}}
          </td>
          <td>
            {{XSS0[0].OtherInfo}}
          </td>

        </tr>
      </table>

    </div>

    <div class="xss1" v-html="XSS1">

    </div>

    <div class="detailsss">
      <el-collapse accordion>
        <el-collapse-item>
          <template slot="title">
            <i class="header-icon el-icon-question"></i>
          </template>
          <div class="details1">
            <p>Cross-Site ScriptingXSSWeb  HTML  JavaScript XSSHTML</p>
            <p> 1.JS ( :  &#60;img src="http:www.xxx.com" onerror="alert('xss')&#62;   )</p>
            <p>2.html ( :  &#60;s&#62;123&#60;/s&#62;  )</p>
            <p>3. ( :  &#60;img  src="https://img1.baidu.com/it/u=3140958831,606641020&fm=26&fmt=auto&gp=0.jpg"&#62; )  </p>
          </div>
        </el-collapse-item>
        <el-collapse-item>
          <template slot="title">
            <i class="header-icon el-icon-error"></i>
          </template>
          <div class="details1">
            <p></p>
            <p></p>
            <p>Web</p>
            <p></p>
            <p></p>
          </div>
        </el-collapse-item>
        <el-collapse-item>
          <template slot="title">
            <i class="header-icon el-icon-s-cooperation"></i>
          </template>
          <div class="details1">
            <p></p>
            <p>1script.img.2;</p>
          </div>

        </el-collapse-item>

      </el-collapse>
    </div>

  </el-main>
</template>

<script>
import { getClassification } from "@/service/api";
import {getAllClassification} from '@/service/api';
//    
export default {
  data(){
    return{
      XSS1:'',
      value:'',
      XSS0:[{
        ClassificationName:'',
        ClassificationID:'',
        OtherInfo:''
      }],
      cities:[],
    }
  },
  methods:{
    handelxss(){
      this.$router.push({
        path:'/main/xss1',
      })
    },
    handelelelxss(){
      getClassification(
          {
            ClassificationID:this.value
          }
      ).then((data)=>{
        if(data.code===200){
          this.XSS0[0].ClassificationName=data.data.ClassificationName,
              this.XSS0[0].ClassificationID=data.data.ClassificationID,
              this.XSS0[0].OtherInfo=data.data.OtherInfo

        }
      })
    },
    handelelxss(){
      console.log(this.value);
      getClassification(
          {
            ClassificationID:this.value
          }
      ).then((data)=>{
        let html='';
        if(data.code===200){
          html=`<table class="tables" border='1' width="100%" >
                            <tr align="center"> 
                                <td>
                                XSS
                                </td>
                                <td>
                                
                                </td>
                                <td>
                               ID
                                </td>
                                <td>
                               
                                </td>                              
                            </tr>
                            <tr align="center"> 
                                <td>
                                XSS
                                </td>
                                <td>
                                ${data.data.ClassificationName}
                                </td>
                                <td>
                                ${data.data.ClassificationID}
                                </td>
                                <td>
                                ${data.data.OtherInfo}
                                </td>                               
                            </tr>
                        </table>`
          this.XSS1=html;
        }
      })
    }
  },
  mounted(){
    getAllClassification().then((data)=>{
      if(data.code===200){
        for(var i=0;i<data.data.length;i++){
          if(data.data[i]){
            if(data.data[i].classificationID!=1){
              this.cities.push({
                value: data.data[i].classificationID,
                label: data.data[i].classificationName
              })
            }

          }
        }
      }
    })
  }
}
</script>
